@import url('https://fonts.googleapis.com/css2?family=Karla:wght@700&display=swap');


body{
  padding: 10px;
  font-family: 'Karla', 'Karla', sans-serif;
    letter-spacing: 0px;
    font-weight: 700;
    font-size: 18px;
}
button{
  border: none;
  border-radius: 3%;
  outline: none;
}
button:focus{
  outline: 0 !important;
  border: 12px double white;
}
.socialLink{
  height: 55px;
  width: 100%;
  background-color: #212121;
  appearance: none;
  font-size: 18px;
  color: white;
  font-family: 'Karla', 'Karla', sans-serif;
  letter-spacing: 0px;
  font-weight: 700;
  font-size: 18px;
  border: 1px solid white;
  border-radius: 10px;
  
}

.socialLink:hover{
  background-color: white;
  color: black;
  font-family: 'Karla', 'Karla', sans-serif;
  letter-spacing: 0px;
  font-weight: 700;
  font-size: 18px;
}
.row{
  margin-bottom: 16px;
}